.swipeme {
  width: 100vw; 
  height: 100%;
  box-sizing: border-box;

  &__content {
    height: 100%;
    display: flex;
    align-items: flex-start;
    overflow: hidden;   
  }

  &__slide {
    min-height: 350px;
    max-height: 55vh;
    overflow-y: scroll;
    min-width: 100vw;
    font-size: 3rem;
   // user-select: none;
  }

  &__dots {
    display: flex;
    justify-content: center;
    padding: 10px 0;
    
    & > div {
      margin: 0 3px;
      transition: .3s;
      border: 1px solid white;
      border-radius: 50%;
      height: 8px;
      width: 8px;

    &.active {
      background-color: hsl(120,0,90);
    }
    }
  }
}