.scramble {
    height: auto;
    padding: 10px 5px 10px 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    cursor: pointer;
    font-size: 1.2rem;
    color: hsl(0,0,96);
    user-select: none;
    -webkit-tap-highlight-color: transparent;
}