.app {
    background: linear-gradient(to bottom, #333461 10%,#aa4583 90%);
    min-height: 100vh;
    max-height: 100vh;
    position: relative;
    display: flex;
    flex-direction: column;
    overflow: hidden; 
}

::-webkit-scrollbar {
    width: 0px;  /* Remove scrollbar space */
    background: transparent;  /* Optional: just make scrollbar invisible */
}